<template>
  <div>
    <div style="width: 100%;overflow: hidden;">
      <mt-header
        style="background: #FFFFFF;color: #000;font-size: 1rem;line-height: 100px;margin-top: 1rem;"
      >
        <router-link to="/" slot="left">
          <mt-button icon="back">确认订单</mt-button>
        </router-link>
      </mt-header>
      <div style="min-height: 19.75rem;padding: 0.7rem;">
        <div
          style="background: #FFFFFF;height: 100%;padding: 0.5rem;border: 1px solid royalblue;"
        >
          <p
            style="border-bottom: solid 1px #CCCCCC;text-align: left;font-size: 1rem;padding-bottom: 0.5rem;"
          >
            庆丰包子铺（窦店）
          </p>
          <p
            style="line-height: 3.25rem;font-size: 1rem;border-bottom: solid 1px #CCCCCC;display: flex;"
            v-for="(i, index) in $store.state.orderList"
            :key="index"
          >
            <span
              style="flex: 1;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;"
              >{{ i.title }}</span
            >
            <span style="flex: 1;text-align: center;">{{ i.num }}</span>
            <span style="flex: 1;float:right">￥{{ i.num * i.jg }}</span>
          </p>
          <p
            style="overflow: hidden;margin-top: 1rem;border-bottom: 1px solid #000;"
          >
            <span style="float: left;">餐盒</span
            ><span style="float: right;">￥{{ num }}</span>
          </p>
          <p
            style="overflow: hidden;text-align: right;margin-top: 0.75rem;font-size: 1.25rem;"
          >
            <span
              style="font-size: 0.7rem;color: #ccc;text-align: left;float: left;"
              >优惠活动说明</span
            >
            <span>小计</span>
            <span>￥{{ totalPrice }}</span>
          </p>
        </div>
      </div>
      <div
        style="position: relative;min-height: 60px;background: black;bottom: 0;overflow: hidden;"
      >
        <p
          style="color: #FFFFFF;line-height: 60px;text-align: left;font-size:2rem;float: left;"
        >
          ￥{{ totalPrice + num }}
        </p>
        <p
          style="float: right;line-height: 60px;padding: 0 2rem;background: #00C98D;color: #FFFFFF;"
        >
          去支付
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "no1",
  data() {
    return {
      arr: [],
      price: "",
      num: 0,
      name: "",
      are: []
    };
  },
  mounted() {
    for (var i = 0; i < this.$store.state.orderList.length; i++) {
      this.num += this.$store.state.orderList[i].num;
    }
  },
  computed: {
    ...mapGetters(["totalPrice"])
  }
};
</script>

