<template>
  <div class="home">
    <mt-header
      fixed
      title="庆丰包子铺（窦店）"
      style="border-bottom: solid 1px #ccc;background: #fff;color: #000000;line-height: 2.5rem;font-size: 1.5rem;"
    ></mt-header>
    <HelloWorld></HelloWorld>

    <div>
      <div style="height: 100%;margin-top: 4rem;">
        <div style="overflow: hidden;">
          <div style="float: left;width: 20%;">
            <div
              style="background: #CCCCCC;height: 3rem;"
              v-for="i in arr"
              :key="i.title"
            >
              <li
                @click="fn(i.title)"
                style="background: #ccc;line-height: 3rem;height: 3rem;border-bottom:1px solid #000;"
              >
                {{ i.title }}
              </li>
            </div>
          </div>
          <div
            style="width: 70%;padding: 0 7rem;float: left;"
            v-for="i in arr"
            :key="i.title"
          >
            <div v-if="inx == i.title">
              <div
                v-for="(a, index) in i.arr"
                :key="index"
                style="margin-bottom: 2rem;"
              >
                <div style="overflow: hidden;">
                  <div style="float: left;width: 20%;">
                    <img
                      src="https://dss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1525199938,525773601&fm=26&gp=0.jpg"
                      alt=""
                      style="width: 4rem;height: 4rem;display: block;"
                    />
                  </div>
                  <div
                    style="float: left;text-align: left;width: 75%;margin-left: 5%;"
                  >
                    <p style="font-size: 1rem;font-weight: 600;">
                      {{ a.title }}
                    </p>
                    <p
                      style="font-size: 0.5rem;color: #d2d1d1;padding: 0.2rem 0;"
                    >
                      {{ a.ice }}
                    </p>
                    <p style="font-size: 0.5rem;color: #d2d1d1;">{{ a.js }}</p>
                    <p
                      style="font-size: 0.7rem;color: red;padding-top: 0.5rem;"
                    >
                      ￥{{ a.jg }}
                    </p>

                    <jia v-model="a.num"></jia>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <p style="position: relative;z-index: 99;">
      总价<span style="padding-right: 1rem;">￥{{ totalPrice }}</span>
    </p>
    <div style="padding-right: 10px;">
      <router-link
        to="/no1"
        style="text-decoration: none;float: right;margin-top: 1rem;text-align: center;color: #fff;line-height: 3rem;"
      >
        <button
          style="width: 5rem;height: 3rem;background: green;line-height: 3rem;border: none;"
        >
          <span style="text-align: center;">去结算</span>
        </button>
      </router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapState, mapGetters } from "vuex";
import HelloWorld from "@/components/HelloWorld.vue";
import jia from "./../views/jia.vue";

export default {
  name: "home",
  components: {
    HelloWorld,
    jia
  },
  watch: {
    arr: {
      handler(v1) {
        this.are = [];
        v1.forEach(el => {
          el.arr.forEach(ele => {
            if (ele.num) {
              this.are.push(ele);
            }
          });
        });
        // 同步到vuex里
        this.$store.commit("addOrder", this.are);
      },
      deep: true
    }
  },
  data() {
    return {
      arr:  [
		{
			"title": "水饺",
			"arr": [
				{
					"title": "猪肉白菜水饺",
					"ice": "月售18份  好评率100%",
					"jg": "16",
					"num":0
				}, {
					"title": "鱿鱼三鲜水饺",
					"ice": "月售18份  好评率100%",
					"jg": "20",
					"num":0
				}, {
					"title": "虾仁三鲜水饺",
					"ice": "月售19份  好评率100%",
					"jg": "25",
					"num":0
				}, {
					"title": "南瓜皮菌菇三鲜猪肉水饺",
					"ice": "月售19份  好评率100%",
					"jg": "24",
					"js": "香菇、猪肉、鸡蛋、木耳、饺子皮",
					"num":0
				}
			]
		} 
	],
      are: [],
      inx: "水饺",
      num: ""
    };
  },
  methods: {
    fn(i) {
      this.inx = i;
    }
  },
  // created() {
  //   this.$http.get("https://web.json").then(data => {
  //     console.log(data.data);
  //     this.arr = data.data.arr;
  //   });
  // },
  computed: {
    ...mapState(["orderList"]),
    ...mapGetters(["totalPrice"])
  }
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
</style>
